import axios from 'axios'
import configService from './config'

const proyectoPIService = {}

  proyectoPIService.guardarDerAut = function(data){

    return new Promise(function(resolve, reject) {axios({
        method:'POST',
        url: configService.apiUrl + '/addProyectoProdDerAut',
        dataType: "json",
        data: {
          data: data          
        }
      })
      .then(function(res) {

        resolve(res)      
        
      })
      .catch(function(err) {     

        resolve(err)
           
      })
    })

  }

